* {
  box-sizing: border-box;
}

html {
  font-family: Roboto;
  font-size: 10px;
  font-weight: 400;
}

body {
  margin: 0;
}

p, ul, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  outline: none;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__item {
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  display: flex;
}

.catalog__list {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 487px) {
  .catalog__list {
    grid-template-columns: repeat(1);
  }
}

@media (width >= 767px) {
  .catalog__list {
    grid-template-columns: repeat(2);
  }
}

@media (width >= 1023px) {
  .catalog__list {
    grid-template-columns: repeat(3);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  line-height: 14px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  text-align: center;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-align: center;
}

.card__link-buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  transition: background-color .3s;
  display: block;
}

.card__link-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  width: min-content;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
